<template>
    <div class="received">
        <div class="bread_crumb">
            <!--<span>位置：主页 | </span>-->
            <span>当前位置：</span>
            <span>快速领用</span><br>
        </div>
        <div class="people">个人信息</div>
        <div class="Information">
            <div class="Left">
                <div class="LeftImg"></div>
                <el-input class="p_search" v-model="teacherNumber" type="password" placeholder="请刷卡识别" @keyup.enter.native="checkTeacherNumber()"></el-input>
            </div>
            <div class="ipt">
                <el-form :label-position="labelPosition" label-width="80px" :model="teacherForm">
                    <el-form-item label="姓名">
                        <el-input :readonly="is_read" unselectable="on" v-model="teacherForm.name"></el-input>
                    </el-form-item>
                    <el-form-item label="性别">
                        <el-input :readonly="is_read" unselectable="on" v-model="teacherForm.sex_type"></el-input>
                    </el-form-item>
                    <el-form-item label="教师编号">
                        <el-input :readonly="is_read" unselectable="on" v-model="teacherForm.id_number"></el-input>
                    </el-form-item>
                    <el-form-item label="职位">
                        <el-input :readonly="is_read" unselectable="on" v-model="teacherForm.position"></el-input>
                    </el-form-item>
                    <el-form-item label="联系方式">
                        <el-input :readonly="is_read" unselectable="on" v-model="teacherForm.call"></el-input>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <!--<el-button @click="connectionClick()" type="primary" style="height:
        30px;line-height: 30px;padding: 0 12px;">连接</el-button>-->
        <div class="people">领用申请记录</div>
        <div class="apply">
            <div class="block block_time">
                <span class="demonstration">开始时间</span>
                <el-date-picker v-model="startValue" type="date" placeholder="选择日期"
                              class="p_search"  @change="changeStartDate()">
                </el-date-picker>
            </div>
            <div class="block block_time">
                <span class="demonstration">结束时间</span>
                <el-date-picker v-model="endValue" type="date" placeholder="选择日期"
                                class="p_search"  @change="changeEndDate()">>
                </el-date-picker>
            </div>
            <div class="Search">
                <div class="search_input el-input el-input--suffix">
                    <input autocomplete="off" v-model="receivedSearch" placeholder="请输入要搜索的申请名称" type="text" rows="2"
                           validateevent="true"
                           suffixicon="el-icon-search" class="el-input__inner p_search"
                           @keyup.enter.active="searchReceivedClick()">
                    <span class="el-input__suffix" @click="searchReceivedClick()">
                        <span class="el-input__suffix-inner">
                            <i class="el-input__icon iconfont el-icon-pharmaceutical_administration-sousuo"></i>
                        </span>
                    </span>
                </div>
            </div>
        </div>
        <!-- 模态框 -->
        <el-button :class="multipleReceivedSelection.length == 0 ? 'unShow' : 'right_btn'"
                   type="primary" @click="connectionClick()">领用核查</el-button>
        <div class="ModelUse" v-if="showNew">
            <div class="Model">
                <div class="ModelT">
                    <span>快速领用</span>
                    <i class="el-icon-close" @click="closeBox()"></i>
                </div>
                <div class="ModelMain">
                    <div class="stock_box" style="height: 430px;">
                        <div class="ModelLeft">
                            <div class="Model_title">申领与扫描对照表</div>
                            <div class="Model_table">
                                <el-table ref="scanningTable" :data="scanningData" stripe tooltip-effect="dark"
                                          style="width: 100%" max-height="379" :row-class-name="tableRowClassName"
                                          @selection-change="handleChooseChange">
                                    <el-table-column type="selection" width="55" :selectable='checkboxInit' ></el-table-column>
                                    <el-table-column prop="item_id" label="物品编号" width="129"></el-table-column>
                                    <el-table-column prop="item_name" label="物品名称" width="129"></el-table-column>
                                </el-table>
                            </div>
                        </div>
                        <div class="ModelIcon">
                            <span class="el-icon-sort-down"></span>
                            <i class="el-icon-sort-down"></i>
                        </div>
                        <div class="ModelRight">
                            <div class="Model_title">扫描后缺少物品列表</div>
                            <div class="Model_table">
                                <el-table ref="lackTable" :data="lackData" stripe tooltip-effect="dark"
                                          style="width: 100%" max-height="379">
                                    <!--<el-table-column type="selection" width="55"></el-table-column>-->
                                    <el-table-column prop="item_name" label="物品名称" width="129"></el-table-column>
                                    <el-table-column label="数量">
                                        <template slot-scope="scope">
                                            <span>{{ scope.row.stock }}{{ scope.row.unit }}</span>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </div>
                        </div>
                    </div>
                    <div class="ModelBtm">扫描结果：合计{{ scanningData.length }}件物品，<span style="color:#f05050;">{{ notIn }}</span>件不在申领表格范围，本次共缺少<span style="color:#f05050;">{{ lackData.length }}</span>件物品，且<span style="color: #f05050">{{ abnormal }}</span>件物品存在异常不可领取，明细请参照上面列表。</div>
                    <el-button class="Model_btn" type="primary" @click="getConfirm()">确认扫描结果无误</el-button>
                </div>
            </div>
        </div>
        <div class="received_table">
            <!--多选-->
            <!--<el-table class="m_table" ref="myReceivedTable" :data="myReceivedList" stripe tooltip-effect="dark"-->
                      <!--style="width: 100%" @selection-change="handleReceivedChange" max-height="300">-->
                <!--<el-table-column type="selection" width="55"></el-table-column>-->
                <!--<el-table-column label="序号" type="index" width="60"></el-table-column>-->
                <!--<el-table-column prop="user_name" label="申领人" width="120"></el-table-column>-->
                <!--<el-table-column prop="item_use" label="申请名称"></el-table-column>-->
                <!--<el-table-column prop="created_at" label="申请时间" show-overflow-tooltip></el-table-column>-->
                <!--<el-table-column prop="list" label="申领物品清单" width="180" show-overflow-tooltip></el-table-column>-->
                <!--<el-table-column prop="approver" label="审批人" show-overflow-tooltip></el-table-column>-->
                <!--<el-table-column prop="remarks" label="备注" show-overflow-tooltip></el-table-column>-->
            <!--</el-table>-->
            <!--单选-->
            <el-table class="m_table" ref="myReceivedTable" :data="myReceivedList" stripe tooltip-effect="dark"
                      style="width: 100%" max-height="300">
                <el-table-column width="55">
                    <template slot-scope="scope">
                        <span :class="singleIndex.id == scope.row.id ? 'click': ''" class="single el-icon-circle-check"
                              @click="handleCurrentChange(scope.row)"></span>
                    </template>
                </el-table-column>
                <el-table-column label="序号" type="index" width="100"></el-table-column>
                <el-table-column prop="user_name" label="申领人"></el-table-column>
                <el-table-column prop="item_use" label="申请名称"></el-table-column>
                <el-table-column label="申请时间">
                    <template slot-scope="scope">
                        <span>{{ scope.row.created_at | r_at }}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="list" label="申领物品清单" show-overflow-tooltip></el-table-column>
                <el-table-column prop="approver" label="审批人" show-overflow-tooltip></el-table-column>
                <el-table-column prop="remarks" label="备注" show-overflow-tooltip></el-table-column>
            </el-table>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            const generateData = _ => {
                const data = [];
                for (let i = 1; i <= 15; i++) {
                    data.push({
                        key: i,
                        label: `备选项 ${ i }`,
                        disabled: i % 4 === 0
                    });
                }
                return data;
            };
            return {
                singleIndex: 0,     //单选 清单id
                teachersList: [],
                labelPosition: "right",
                is_read: true,
                teacherNumber: '',              //教师刷卡编号
                teacherForm: {},
                receivedSearch: "",             //搜索值
                showNew: false,                 //显示新增入库框
                myReceivedList: [],             //我的领用清单列表
                multipleReceivedSelection: [],  //多选中的清单列表
                manifestGoodsList: [],          //某个清单的物品列表
                scanningData: [],               //扫描到的数据
                lackData: [],                   //扫描后清单中缺少的数据
                notIn: 0,                       //不在清单中的物品数
                abnormal: 0,                    //异常物品 （暂为接了未归还又借的 （status == 1））
                chooseSelection: [],            //扫描后的多选（确认领取的数据）
                //搜索时间
                pickerOptions1: {
                    disabledDate(time) {
                        return time.getTime() > Date.now();
                    },
                    shortcuts: [
                        {
                            text: "今天",
                            onClick(picker) {
                                picker.$emit("pick", new Date());
                            }
                        },
                        {
                            text: "昨天",
                            onClick(picker) {
                                const date = new Date();
                                date.setTime(date.getTime() - 3600 * 1000 * 24);
                                picker.$emit("pick", date);
                            }
                        },
                        {
                            text: "一周前",
                            onClick(picker) {
                                const date = new Date();
                                date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                                picker.$emit("pick", date);
                            }
                        }
                    ]
                },
                startValue: "",
                endValue: "",
                receive_at:"",                  //领用时间
                overdue_at:'',                  //逾期时间
                //webSocket
                ws:'',
                timer: '',                      //启动websocket连接
            };
        },
        created() {
            this.$store.state.sideIndex = -1;
        },
        mounted(){
            this.getTeacherLists();//获取教师列表
        },
        beforeDestroy() {
            if(this.ws){
                this.ws.close();
            }
            if(this.timer) { //如果定时器还在运行 或者直接关闭，不用判断
                clearInterval(this.timer); //关闭
            }
        },
        methods: {
            //获取教师列表
            getTeacherLists(){
                this.$axios.post('/user-list',{
                    number: 9999999
                }).then((response) => {
//                    console.log(response);
                    this.teachersList = response.data.data.data;
                }).catch((error) => {})
            },
            //刷卡
            checkTeacherNumber(){
                if(this.teacherNumber == ''){
                    this.$message({
                        message: '请刷卡！',
                        type: 'warning',
                        duration: '1000',
                    });
                } else if(this.teacherNumber == 0){
                    this.$message({
                        message: '该卡无教师信息！',
                        type: 'warning',
                        duration: '1500',
                    });
                    this.teacherNumber = '';
                } else {
                    this.$axios.post('/user-list',{
                        number: 999999999,
                        id_number: this.teacherNumber
                    }).then((response) => {
//                    console.log(response)
                        if(response.data.data.data == undefined){
                            //该卡无教师信息
                            this.$message({
                                message: '该卡无教师信息！',
                                type: 'warning',
                                duration: '1500',
                            });
                            this.teacherForm = {};
                            this.myReceivedList = [];
                        } else {
                            this.$message({
                                type: 'success',
                                message: '刷卡成功!',
                                duration: '1500',
                            });
                            this.teacherForm = response.data.data.data[0];
                            if(this.teacherForm.sex == 1){
                                this.teacherForm.sex_type = "男";
                            } else {
                                this.teacherForm.sex_type = "女";
                            }
                            this.checkMyList();
                        }
                        this.teacherNumber = '';
                    }).catch((err) => {})
                }
            },
            //查看我的清单列表
            checkMyList() {
                this.myReceivedList = [];
                this.$axios.post('/manifest-list', {
                    user_id: this.teacherForm.id
                }).then((response) => {
//				    console.log(response);
//                    this.myReceivedList = response.data.data;//全部
                    for(let i in response.data.data){//未领取
                        if(response.data.data[i].is_receive == 1){
                            this.myReceivedList.push(response.data.data[i])
                        }
                    }
//                    //获取申请人name
//                    for(let x in this.myReceivedList){
//                        for(let i in this.teachersList){
//                            if(this.myReceivedList[x].user_id == this.teachersList[i].id){
//                                this.myReceivedList[x].user_name = this.teachersList[i].name
//                            }
//                        }
//                    }
                }).catch((error) => {})
            },
            //搜索
            searchReceivedClick() {
                this.myReceivedList = [];
                this.$axios.post('/manifest-list', {
                    user_id: this.teacherForm.id,
                    item_use: this.receivedSearch,
                    t_start: this.startValue,
                    t_end: this.endValue,
                }).then((response) => {
//				    console.log(response);
//                    this.myReceivedList = response.data.data;
                    for(let i in response.data.data){//未领取
                        if(response.data.data[i].is_receive == 1){
                            this.myReceivedList.push(response.data.data[i])
                        }
                    }
//                    //获取申请人name
//                    for(let x in this.myReceivedList){
//                        for(let i in this.teachersList){
//                            if(this.myReceivedList[x].user_id == this.teachersList[i].id){
//                                this.myReceivedList[x].user_name = this.teachersList[i].name
//                            }
//                        }
//                    }
                }).catch((error) => {})
            },
            //时间转化
            changeStartDate() {
                if (this.startValue == null) {
                    this.startValue = '';
                } else {
                    let date = new Date(this.startValue);
                    let Y = date.getFullYear() + '-';
                    let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
                    let D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
                    this.startValue = Y + M + D;
                }
            },
            changeEndDate() {
                if (this.endValue == null) {
                    this.endValue = '';
                } else {
                    let date = new Date(this.endValue);
                    let Y = date.getFullYear() + '-';
                    let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
                    let D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
                    this.endValue = Y + M + D;
                }
            },
            //领用单选
            handleCurrentChange(val){
//                console.log(val)
                if(this.multipleReceivedSelection.length == 1){
                    if(this.multipleReceivedSelection[0] == val){
                        //清空
                        this.singleIndex = 0;
                        this.multipleReceivedSelection = [];
                    } else {
                        //选取
                        this.singleIndex = val;
                        let arr = [];
                        arr.push(val);
                        this.multipleReceivedSelection = arr;
                        this.getManifestGood();
                    }
                } else {
                    //选取
                    this.singleIndex = val;
                    let arr = [];
                    arr.push(val);
                    this.multipleReceivedSelection = arr;
                    this.getManifestGood();
                }
            },
            //多选  改变
            handleReceivedChange(val) {
//			    console.log(val)
                this.multipleReceivedSelection = val;
                //多选为一 时获取清单物品列表
			    if(val.length == 1){
                    this.getManifestGood();
                }
            },
            //领用时间和逾期时间的转换
            changeSomeTime(){
                //领用时间
                let date = new Date();
                let Y = date.getFullYear() + '-';
                let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
                let D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
                this.receive_at = Y + M + D;
                //逾期时间
                let over = new Date(Date.parse(new Date()) + 7*24*60*60*1000);
                let Y2 = over.getFullYear() + '-';
                let M2 = (over.getMonth() + 1 < 10 ? '0' + (over.getMonth() + 1) : over.getMonth() + 1) + '-';
                let D2 = over.getDate() < 10 ? '0' + over.getDate() : over.getDate();
                this.overdue_at = Y2 + M2 + D2;
            },
            //获取清单物品列表
            getManifestGood(){
                this.$axios.post('/apply-list',{
                    manifest_id: this.multipleReceivedSelection[0].id,
                }).then((response) => {
//                    console.log(response)
                    this.manifestGoodsList = response.data.data;
                    //获取大类id
                    this.$axios.post('/sort-list')
                        .then((res) => {
//                            console.log(res)
                            for(let i in this.manifestGoodsList){
                                for(let x in res.data.data){
                                    if(this.manifestGoodsList[i].item_name == res.data.data[x].name){
                                        this.manifestGoodsList[i].sort_id = res.data.data[x].id
                                    }
                                }
                            }
                        }).catch(error=>{})
                }).catch(err=>{})
            },
            //连接
            connectionClick(){
                let self = this;
                this.ws = new WebSocket('ws://127.0.0.1/tag-pool');
//                console.log(this.ws)
                this.ws.onopen = function (evt) {
                    self.showNew = true;
                    //重置
                    self.notIn = 0;
                    self.abnormal = 0;
                    self.scanningData = [];
                    self.lackData = [];
                };

                let arr = [];
                this.ws.onmessage = function (event) {
//                    console.log(event.data)
                    let cardArr = [];
                    var data = event.data;
//                    console.log(data)
                    if(JSON.parse(data).length == 0){
                        //没有扫到标签 显示缺少的数据
                        self.lackData = self.manifestGoodsList;
//                        self.scanningData = []; //清楚原本显示的数据
//                        self.notIn = 0;
//                        self.abnormal = 0;
                    }
                    if(arr.sort().toString() == JSON.parse(data).sort().toString()){
                    } else {
                        arr = JSON.parse(data);
                        console.log(arr)
                        for(let m=0;m<arr.length;m++){
                            if(arr[m] == null ){

                            } else {
                                cardArr.push(arr[m].epc)
                            }
                        }
//                        console.log(cardArr)
                        //获取扫描到的物品列表
                        self.scanningData = [];
                        self.lackData = [];
                        self.notIn = 0;
                        self.abnormal = 0;
                        if(cardArr.length == 0){
                        } else {
                            self.$axios.post('/frid-sel',{
                                frid_list: cardArr
                            }).then((response) => {
//                                console.log(response);
                                if(response.data.message == "无数据"){
                                    //有未绑定的标签 导致无数据返回
                                    self.$message({
                                        type: 'warning',
                                        message: '请勿扫描未绑定的标签!',
                                        duration: '2000',
                                    });
                                    //显示缺少的数据？？
//                                    self.lackData = self.manifestGoodsList
                                } else {
                                    //重置
                                    self.notIn = 0;
                                    self.abnormal = 0;
                                    self.scanningData = [];
                                    self.lackData = [];
                                    // 需要将清单物品列表重置
                                    for(let i in self.manifestGoodsList){
                                        self.manifestGoodsList[i].isHas = 2;
                                    }
                                    //扫描到的数据
                                    for(let x in response.data.data){
                                        self.scanningData.push({
                                            item_name: response.data.data[x][0].item_name,
                                            item_id: response.data.data[x][0].item_id,
                                            frid_number: response.data.data[x][0].frid_number,
                                            sort_id: response.data.data[x][0].sort_id,
                                            status: response.data.data[x][0].status,
                                        })
                                    }
//                                    console.log(self.scanningData)
                                    //扫描到的物品与清单下的物品列表比较  分别是我的  还是不是我的
                                    for(let m in self.scanningData){
                                        for(let n in self.manifestGoodsList){//(相同的小类名称)
                                            if(self.manifestGoodsList[n].item_name == self.scanningData[m].item_name){
                                                //如果有相等的
                                                self.scanningData[m].isHas = 1;     //是我的
                                                self.manifestGoodsList[n].isHas = 1;  //这里也放 用于获取没有扫描到的清单物品
                                                break;
                                            } else {
                                            }
                                        }
                                    }
                                    //计算不在清单列表的物品数量 和异常物品
                                    for(let s in self.scanningData){
                                        if(self.scanningData[s].isHas == 1){
                                        } else {
                                            self.notIn ++;
                                        }
                                        if(self.scanningData[s].status == 1){
                                            self.abnormal ++;
                                        }
                                    }
                                    //获取缺少的数据
                                    for(let i in self.manifestGoodsList){
                                        if(self.manifestGoodsList[i].isHas == 1){
                                        } else {
                                            self.lackData.push(self.manifestGoodsList[i])
                                        }
                                    }
                                }
                            }).catch((err) => {})
                        }
                    }
                };
                this.ws.onclose = function (evt) {};
                this.ws.onerror = function(event) {
//                    console.log(event)
                    self.$message({
                        message: '请先连接硬件并开启软件！',
                        type: 'warning',
                        duration: '2000',
                    });
                    self.ws = '';
                };
            },
            //多选不可点
            checkboxInit(row,index){
//                console.log(row)
                if (row.status == 1) {
                    return 0;//不可勾选
                } else return 1;//可勾选
            },
            tableRowClassName({row, rowIndex}) {
//                console.log({row, rowIndex})
                if(row.status == 1){
                    //如果异常
                    if(row.isHas == 1){
                        //清单中有扫描到的这个物品 (我的列表下)
                        return 'warning-abnormal'
                    } else {
                        return 'warning-more';  //不是我的  变红
                    }
                } else {
                    if(row.isHas == 1){
                        //清单中有扫描到的这个物品 (我的列表下)
                    } else {
                        return 'warning-more';  //不是我的  变红
                    }
                }
            },
            handleChooseChange(val){
//                console.log(val)
                this.chooseSelection = val;
            },
            //关闭弹框
            closeBox() {
                this.scanningData = [];
                this.lackData = [];
                this.showNew = false;
            },
            //确认无误选择领取
            getConfirm(){
                if(this.chooseSelection.length == 0){
                    this.$message({
                        message: '请先选择最终要领取的物品！',
                        type: 'warning',
                        duration: '1500',
                    });
                } else {
                    //获取领用时间和逾期时间
                    this.changeSomeTime();
                    //提交领取物品时需要用的字段
//                    let number = "";  //不传  后台获取items表中的原数据
//                    let site_use = "";  //使用地点   暂时为空
//                    let item_id = "";
                    let arr = this.chooseSelection;
                    let remark = '';
                    if(this.multipleReceivedSelection[0].remarks == null){
                        remark = "无"
                    } else {
                        remark = this.multipleReceivedSelection[0].remarks;
                    }
                    for(let i in arr){
                        arr[i].user_id = this.multipleReceivedSelection[0].user_id;
                        arr[i].manifest_id = this.multipleReceivedSelection[0].id;
                        arr[i].site_use = "无";   //使用地点
                        arr[i].remarks = remark;
                        arr[i].item_use = this.multipleReceivedSelection[0].item_use;
                        arr[i].approver = this.multipleReceivedSelection[0].approver;
                        arr[i].recipient = JSON.parse(localStorage.getItem("user")).name;      //经办人
                        arr[i].receive_at = this.receive_at;
                        arr[i].overdue_at = this.overdue_at;
                    }
                    this.$axios.post('/receive-edit',{
                        receive: arr,
                        manifest_id: this.multipleReceivedSelection[0].id,
                    }).then((response) => {
//                        console.log(response);
                        if(response.data.status == 0){
                            this.$message({
                                message: '领用成功！',
                                type: 'success',
                                duration: '1500',
                            });
                            this.checkMyList();
                            this.showNew = false;
                        }
                    }).catch((err) => {})
                }
            },
            //去重
            unique(array) {
                var obj = {};
                return array.filter(function(item, index, array){
                    //console.log(typeof item + JSON.stringify(item))
                    return obj.hasOwnProperty(typeof item + JSON.stringify(item)) ? false : (obj[typeof item + JSON.stringify(item)] = true)
                })
            },
        },
        filters: {
            //领用时间去除时分秒
            r_at: function (value) {
                if(value){
                    return value.split(' ')[0];
                } else {
                    return '';
                }
            },
        }
    };
</script>

